import { t } from '../../../helpers/i18n/dictionary';
import useHomeAttributes from '../../../hooks/profiles/useHomeAttributes';
import useStaticFiles from '../../../hooks/staticFiles/useStaticFiles';
import AttributeGroup from '../../Attribute/AttributeGroup/AttributeGroup';
import ActionButton from '../../Buttons/ActionButton';
import Cloud from '../../Icons/Cloud/Cloud';
import PageMeta from '../../Layout/PageMeta/PageMeta';
import Section from '../../Sections/Section';

const Website = () => {
  const { data } = useHomeAttributes().fetch;

  if (!data) {
    return <></>;
  }

  return (
    <section>
      <PageMeta icon={Cloud} title={t('Website Configuration')} />
      <AttributeGroup attributes={data.home} groupTitle={t('Home')} />
      <AttributeGroup attributes={data.theme} groupTitle={t('Theme')} />
      <PublishOptions />
    </section>
  );
};

const PublishOptions = () => {
  const { mutate: publishFile, status: publishStatus } = useStaticFiles().publish;

  return (
    <>
      <Section
        isOpaqueBg={true}
        title={
          <>
            <Cloud className="inline-block h-4 w-4" /> {t('Publish your public data')}
          </>
        }
      >
        <div className="flex flex-row">
          <ActionButton
            onClick={() => publishFile()}
            state={publishStatus}
            icon="save"
            className="ml-auto"
          >
            {t('Publish static file')}
          </ActionButton>
        </div>
      </Section>
    </>
  );
};

export default Website;
